import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import "../Params/index.scss"

export default function Params() {
  const { title } = useParams();

  const paramArray = [
    {
      id: 0,
      title: title === "zhivye-foto" ? " " : "",
    },
    {
      id: 1,
      title: title === "luchshie-foto" ? "  " : "",
    },
    {
      id: 2,
      title: title === "zhivye-foto-2" ? "   " : "",
    },
    {
      id: 3,
      title: title === "shumka" ? "" : "",
    },
    {
      id: 4,
      title: title === "detaly" ? "  " : "",
    },
    {
      id: 5,
      title: title === "video" ? "" : "",
    },
    
  ];

  const [Path, setPath] = useState([]);
  useEffect(() => {
    setPath(paramArray);
  }, []);

  if (title) {
    return (
      <div className="Glava">
        <Link to={"/"}></Link>
        <span>/</span>

        {Path.map((pathName) => {
          const { id, title } = pathName;
          return <span key={id}>{title}</span>;
        })}
      </div>
    );
  }
}
